<template>
  <div>
    <h1 class="heading-primary">Quiz</h1>
    <p class="heading-secondary">
      Please answer all questions to test your knowledge about <b>English</b>
    </p>
    <div v-if="!questionShown" class="container">
      <section class="section-quiz text-center">
        <button @click="startGame" class="btn">
          Start Game
        </button>
      </section>
    </div>
    <div class="container" v-else>
      <section class="section-quiz">
        <Question
          :question="questions[currentQuestionIndex].question"
          :options="questions[currentQuestionIndex].answers"
        ></Question>
        <div class="cta-buttons">
          <button @click="submitAnswer" class="btn" :disabled="submitDisabled">
            Submit
          </button>
          <button @click="nextQuestion" v-show="showNextBtn" class="btn">
            Next
          </button>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import Question from './components/Question.vue';
export default {
  components: { Question },
  name: 'App',
  data() {
    return {
      currentQuestionIndex: 0,
      showNextBtn: false,
      questionShown: false,
      questions: [
        {
          question: `Where do you live?`,
          answers: [
            { text: `I am live in London`, correct: false },
            { text: `My live is London.`, correct: false },
            { text: `I live in London.`, correct: true },
            { text: `I lives in London`, correct: false },
          ],
        },
        {
          question: `Choose the correct answer`,
          answers: [
            { text: `He has two brothers.`, correct: true },
            { text: `I has two brothers`, correct: false },
            { text: `He got two brothers.`, correct: false },
            { text: `He two brothers.`, correct: false },
          ],
        },
        {
          question: `Choose the correct answer`,
          answers: [
            { text: `Me like coffee.`, correct: false },
            { text: `I like coffee.`, correct: true },
            { text: `I likes the coffee`, correct: false },
            { text: `Me don't like coffee.`, correct: false },
          ],
        },
        {
          question: `Choose the correct answer`,
          answers: [
            { text: `She lives on your street.`, correct: true },
            { text: `She live on your street.`, correct: false },
            { text: `She do on your street.`, correct: false },
            { text: `She does on your street.`, correct: false },
          ],
        },
        {
          question: `Peter and Sidney are......Australia.`,
          answers: [
            { text: `from`, correct: true },
            { text: `by`, correct: false },
            { text: `for`, correct: false },
            { text: `per`, correct: false },
          ],
        },
        {
          question: `Choose the correct answer`,
          answers: [
            { text: `They is Italian.`, correct: false },
            { text: `They is Italy.`, correct: false },
            { text: `They are Italian.`, correct: true },
            { text: `They Italian.`, correct: false },
          ],
        },
        {
          question: `Are you German?`,
          answers: [
            { text: `No. My are American.`, correct: false },
            { text: `No. My is American.`, correct: false },
            { text: `No. I is American.`, correct: false },
            { text: `No. I am American.`, correct: true },
          ],
        },
        {
          question: `Do you like music?`,
          answers: [
            { text: `Yes, I does.`, correct: false },
            { text: `Yes, I like.`, correct: false },
            { text: `Yes, I do.`, correct: true },
            { text: `Yes, I am.`, correct: false },
          ],
        },
        {
          question: `We walk......work every day.`,
          answers: [
            { text: `to`, correct: true },
            { text: `by`, correct: false },
            { text: `at`, correct: false },
            { text: `of`, correct: false },
          ],
        },
        {
          question: `We don't go......train.`,
          answers: [
            { text: `to`, correct: false },
            { text: `by`, correct: true },
            { text: `at`, correct: false },
            { text: `out`, correct: false },
          ],
        },
        {
          question: `My birthday is......the third of March.`,
          answers: [
            { text: `to`, correct: false },
            { text: `on`, correct: true },
            { text: `at`, correct: false },
            { text: `out`, correct: false },
          ],
        },
        {
          question: `Do you go to the beach......summer?`,
          answers: [
            { text: `in`, correct: true },
            { text: `on`, correct: false },
            { text: `at`, correct: false },
            { text: `out`, correct: false },
          ],
        },
        {
          question: `I live......Rosemary Road.`,
          answers: [
            { text: `from`, correct: false },
            { text: `on`, correct: false },
            { text: `at`, correct: true },
            { text: `out`, correct: false },
          ],
        },
        {
          question: `I got a dog......my birthday!`,
          answers: [
            { text: `from`, correct: false },
            { text: `on`, correct: false },
            { text: `for`, correct: true },
            { text: `out`, correct: false },
          ],
        },
        {
          question: `She is married......a doctor.`,
          answers: [
            { text: `from`, correct: false },
            { text: `on`, correct: false },
            { text: `to`, correct: true },
            { text: `out`, correct: false },
          ],
        },
        {
          question: `You can come......me.`,
          answers: [
            { text: `from`, correct: false },
            { text: `on`, correct: false },
            { text: `with`, correct: true },
            { text: `out`, correct: false },
          ],
        },
        {
          question: `We always eat chicken......Christmas.`,
          answers: [
            { text: `from`, correct: false },
            { text: `on`, correct: true },
            { text: `with`, correct: false },
            { text: `out`, correct: false },
          ],
        },
        {
          question: `Spot the different one`,
          answers: [
            { text: `man`, correct: false },
            { text: `cat`, correct: true },
            { text: `boy`, correct: false },
            { text: `girl`, correct: false },
          ],
        },
        {
          question: `Spot the different one`,
          answers: [
            { text: `car`, correct: false },
            { text: `motorcycle`, correct: false },
            { text: `truck`, correct: false },
            { text: `bike`, correct: true },
          ],
        },
        {
          question: `Spot the different one`,
          answers: [
            { text: `airport`, correct: false },
            { text: `train station`, correct: false },
            { text: `bus station`, correct: false },
            { text: `market`, correct: true },
          ],
        },
      ],
    };
  },
  computed: {
    submitDisabled() {
      // disable submit button if "Next" btn is shown
      return this.showNextBtn ? true : false;
    },
  },
  methods: {
    startGame() {
      this.questionShown = !this.questionShown;

      // randomize questions and answers
      let randomizedQuestions = [...this.questions];
      randomizedQuestions = this.randomizeArray(randomizedQuestions);
      randomizedQuestions.map((question) => {
        question.answers = this.randomizeArray(question.answers);
      });
      this.questions = randomizedQuestions;
    },
    nextQuestion() {
      this.currentQuestionIndex++;
      this.showNextBtn = !this.showNextBtn;
    },
    submitAnswer() {
      this.showNextBtn = !this.showNextBtn;
    },
    randomizeArray(arr) {
      // fisher yates shuffle
      let counter = arr.length;

      while (counter > 0) {
        let index = Math.floor(Math.random() * counter);

        counter--;

        let temp = arr[counter];
        arr[counter] = arr[index];
        arr[index] = temp;
      }

      return arr;
    },
  },
};
</script>

